<!--  -->
<template>
  <div>

    <!--   start -->
    <nav class="navbar navbar-inverse navbar-fixed-top">
        <div class="container">
            <div class="navbar-header">
                    <router-link class="navbar-brand" to="/"><i class="iconfont icon-ntilde" @click="back"></i>TLPrince</router-link>
                    <button class="navbar-toggle collapsed" data-toggle="collapse" data-target="#mynav" aria-expanded="false">
                            <span class="icon-bar"></span>
                            <span class="icon-bar"></span>
                            <span class="icon-bar"></span>
                    </button>
            </div>

            <div class="collapse navbar-collapse" id="mynav">
                    <ul class="nav navbar-nav nav-main">
                        <li><router-link to="/homes"></router-link></li>
                        <li><router-link to="/homes/comic"></router-link></li>
                        <li><router-link to="/homes/gufeng"></router-link></li>
                        <li><router-link to="/homes/artwork"></router-link></li>
                        <li><router-link to="/homes/aboutme"></router-link></li>
                    </ul>

                    <ul class="nav navbar-nav navbar-right">
                        <li v-if="flag">
                            <a class="dropdown">
                                <div class="dropdown-toggle" data-toggle="dropdown">
                                    
                                    <span class="caret"></span>
                                </div>
                                <ul class="dropdown-menu">
                                    <li>
                                        <a  href="#" data-toggle="modal" data-target="#resgister"></a>
                                    </li>
                                    <li class="divider"></li>
                                    <li>
                                        <a href="#" data-toggle="modal" data-target="#login"></a>
                                    </li>
                                </ul>
                            </a>
                        </li>
                        <li v-if="!flag">
                            <a href="#">Hi~ </a>
                        </li>
                        <li v-if="flag">
                            <a href="#"></a>
                        </li>
                        <li v-if="!flag" >
                            <router-link to="/homes/publisharticle"></router-link>
                        </li>
                        <li v-if="!flag" @click="logout">
                            <a href="javascript:void(0)"></a>
                        </li>
                    </ul>
            </div>
        </div>
    </nav>
    <!--   end -->
    <transition
        enter-active-class="zoomInLeft"
        leave-active-class="zoomOutLeft"
        :duration="{ enter: 800, leave: 800}">
        <router-view class="animated"></router-view>
    </transition>

    <!--   start -->
    <div class="footer">TlPrince By  :ICP18003194-1</div>
    <!--   end -->
    
    <!--   start -->
    <div class="modal fade" id="resgister">
        <div class="modal-dialog modal-sm">
            <div class="modal-content">
                <div class="modal-header">
                    <button class="close" data-dismiss="modal"><span>&times;</span></button>
                    <h4 class="modal-title"></h4>
                </div>
                <div class="modal-body">
                    <form action="">
                        <div class="form-group">
                            <label for="">:</label>
                            <input type="" name="" class="form-control" ref="l_username">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="password" name="" class="form-control" ref="l_password">
                        </div>
                        <div class="checkbox">
                            <label>
                                <input type="checkbox" name="">
                            </label>
                            <label>
                                <a href=""></a>
                            </label>
                        </div>
                    </form>
                </div>
                <div class="modal-footer">
                    <button class="btn btn-default pull-right" data-dismiss="modal">close</button>
                    <button class="btn btn-info pull-right" data-dismiss="modal" @click="login"></button>
                </div>
            </div>
        </div>
    </div>
    <!--   end -->

    <!--   start -->
    <div class="modal fade" id="login">
        <div class="modal-dialog modal-sm">
            <div class="modal-content">
                <div class="modal-header">
                    <button class="close" data-dismiss="modal"><span>&times;</span></button>
                    <h4 class="modal-title"></h4>
                </div>
                <div class="modal-body">
                        <div class="form-group">
                            <label>:</label>
                            <input type="text" name="username" class="form-control" ref="r_username">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="password" name="password" class="form-control" ref="r_password">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="password" name="repassword" class="form-control">
                        </div>
                        <div class="radio">
                            <label>
                                <input type="radio" name="sex">
                            </label>
                            <label>
                                <input type="radio" name="sex">
                            </label>
                        </div>
                        
                </div>
                <div class="modal-footer">
                    <button class="btn btn-danger pull-right success" @click="reg" data-dismiss="modal"></button>
                </div>
            </div>
        </div>
    </div>
    <!--   end -->

  </div>
</template>

<script>
import axios from "axios"
export default {
    data () {
        return {
            flag: true
        };
    },

    created(){
        if(this.getCookie('uid')){
            console.log('ok')
            this.flag = !this.flag
        }
    },
    mounted(){
        console.log(document.cookie)
    },
    methods: {
        // 
        reg(){
            const username = this.$refs.r_username.value
            const password = this.$refs.r_password.value
            axios({
                method: 'post',
                url: '/reg',
                responseType: 'json',
                data: {
                    "username": username,
                    "password": password,
                },
            })
            .then( res => {
                alert(res.data.msg)
            })
        },
        // 
        login(){
            const username = this.$refs.l_username.value
            const password = this.$refs.l_password.value
              axios({
                method: "post",
                url: "/login",
                data: {
                    username: username, //appid
                    password: password,  //secret
                },
                responseType: 'json'
            }).then( res => {
                alert(res.data.msg)
                if(res.data.status == 1){
                    this.flag = !res.data.session.flag
                }
            })
        },
        // 
        logout(){
            axios.get('/logout').then((res) => {
                const con = confirm("")
                if(con == true){
                    location.href = "http://localhost:8080/"
                    this.flag = res.data.flag
                }
            })
        },
        // 
        back(){
            history.go(-1)
        },
        getCookie(c_name){
            if (document.cookie.length>0){
                let c_start=document.cookie.indexOf(c_name + "=")
                if (c_start!=-1)
                    { 
                    c_start=c_start + c_name.length+1 
                    let c_end=document.cookie.indexOf(";",c_start)
                    if (c_end==-1) c_end=document.cookie.length
                    return unescape(document.cookie.substring(c_start,c_end))
                } 
            }
            return ""
        }
    }
}

</script>
<style scoped>
    .footer{
        float: left;
        width: 100%;
        height: 50px;
        text-align: center;
        line-height: 50px;
        background: #000000;
    }
    .navbar .icon-ntilde{
        display: none;
        margin-right: 15px;
    }
    .navbar{
        font-size: 14px;
    }
    @media screen and (max-width: 400px) {
    .page_logo{
        display: none !important;
    }
    .navbar{
        margin: 0
    }
    .navbar .icon-ntilde{
        display: inline;
    }
}
    .animated{
        padding-top: 25px
    }
</style>


