<!--  -->
<template>
    <div>
        <div class="row">
            <!--   start -->
            <form action="/publish" method="post">
                <!--   start -->
                <div class="form-group col-md-6 col-xs-12">
                    <label></label>
                    <select class="form-control" name="kind" ref="kind">
                        <option></option>
                        <option></option>
                        <option></option>
                        <option>cosplay</option>
                        <option></option>
                    </select>
                </div>
                <!--   end -->

                <!--   start -->
                <div class="form-group col-md-6 col-xs-12">
                    <label for=""></label>
                    <input class="form-control" type="text" placeholder="title" name="title" ref="title">
                </div>
                <!--   end -->

                <!--   start -->
                <div class="layui-upload">
                    <button type="button" class="layui-btn" id="upload"></button>
                    <div class="layui-upload-list">
                        <img class="layui-upload-img" id="demo1">
                        <p id="demoText"></p>
                    </div>
                </div>   
                <!--   end -->

                <!--   start -->
                <textarea class="layui-textarea" id="articleArea" style="display: none" placeholder="content..." name="content" ref="content">  
                </textarea>
                <!--   end -->

                <button class="btn btn-info" type="submit"></button>
            </form>
            <!--   end -->
        </div>
    </div>
  
</template>

<script>
import axios from 'axios'
export default {
    data () {
        return {
        };
    },
    
    mounted(){
        this.textareaConfig()
        this.uploadConfig()
    },

    methods: {
        // layui
        textareaConfig(){
            layui.use('layedit', function(){
                let layedit = layui.layedit
                    $ = layui.jquery;
                //
                var index = layedit.build('articleArea',{
                    height:300,
                    float: "left"
                });
            })
        },
        // layui
        uploadConfig(){
            layui.use(["upload", "layer"], () => {
                const $ = layui.$
                const upload = layui.upload
                const layer = layui.layer

                //
                upload.render({
                    elem: '#upload'
                    ,url: '/upload'
                    ,size: 1024*3 // KB
                    ,done(res){
                    layer.alert(res.message)
                    }
                });
            })
        },
    }
}

</script>
<style scoped>
    .row{
        padding: 50px 30px;
    }
    form label{
        margin-top: 15px;
    }
    button{
        margin: 15px 0;
    }
    .layui-layedit{
        float: left !important;
    }
</style>


